<template>
  <transition name="slide">
    <div class="modal" v-if="show">
        <div class="modal-box">
          <div class="modal-head">提示：</div>
          <div class="modal-txt">你确定删除第{{ delIndex +1 }}条数据吗？</div>
          <div class="modal-btn">
            <button class="btn btn-del" @click="submit">确定</button>
            <button class="btn btn-add"  @click="cancel">取消</button>
          </div>
        </div>
    </div>
  </transition>
</template>
<script>
export default {
  props: {
    show: {
      type: Boolean,
      default: false
    },
    delIndex: {
      type: Number,
      default: null
    }
  },
  methods: {
    submit() {
      this.$emit('confirm')
    },
    cancel() {
      this.$emit('update:show', false)
    }
  }
}
</script>

<style lang="less" scoped>
.modal{
  width: 100vw;
  height: 100vh;
  position: fixed;
  overflow: hidden;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, .3);
  .modal-box{
    width: 300px;
    height: 120px;
    background: #ffffff;
    margin: 10% auto;
    padding: 20px;
    border-radius: 5px;
  }
  .modal-head{
    margin-bottom: 20px;
    font-weight: bold;
    color: #FF9900;
  }
  .modal-txt{
    margin: 20px auto;
  }
  .modal-btn{
    margin: 20px auto;
    text-align: center;
  }
}
.slide-enter-active,
.slide-leave-active {
  transition: all .5s ease;
  opacity: 1;
  filter: blur(0)
}
.slide-enter, .slide-leave-to {
  opacity: 0;
  filter: blur(2px)
}
</style>



