<template>
  <div class="edit">
    <div class="add-box" @keyup.enter="addData">
      <label for="name">名称：</label><input type="text" v-model.trim="name" class="input-name">
      <label for="name">数量：</label><input type="text" v-model.number="num" class="input-num">
      <button class="btn" :class="{'btn-add':allowAdd}" @click="addData">添加</button>
    </div>
    <table class="list-table">
      <tr>
        <th>序号</th>
        <th>名称</th>
        <th>数量</th>
        <th>更新时间</th>
        <th>操作</th>
      </tr>
      <tr v-if="!listData.length">
        <td class="no-data" colspan="5">暂无数据</td>
      </tr>
      <tr v-if="listData.length" v-for="(item,index) in listData" :key="index">
        <td>{{index+1}}</td>
        <td>{{item.name}}</td>
        <td>{{item.num}}</td>
        <td>{{item.time | formatDate}}</td>
        <td>
          <button class="btn btn-del" @click="del(index)">删除</button>
        </td>
      </tr>
    </table>
    <my-modal :show.sync="showModal" :delIndex="delIndex" @confirm="delData"></my-modal>
  </div>
</template>
<script>
import MyModal from '@/components/MyModal';
export default {
  name: "edit",
  data() {
    return {
      name: "",
      num: "",
      listData: [],
      showModal: false,
      delIndex: null
    };
  },
  computed: {
    fomartNum() {
      return parseInt(this.num) ? parseInt(this.num) : 0
    },
    allowAdd() {
      return this.name && this.num ? true : false
    }
  },
  methods: {
    addData() {
      if(!this.allowAdd) {
        return false
      }
      this.listData.push({
        name: this.name,
        num: this.fomartNum,
        time: +new Date()
      })
      this.name = ''
      this.num = ''
    },
    del(index) {
      this.delIndex = index
      this.showModal = true
    },
    delData(){
      this.showModal = false
      this.listData.splice(this.delIndex,1)
    }
  },
  components: {
    MyModal
  }
};
</script>
<style lang="less" scoped>
.input-name,
.input-num {
  margin: 0 15px 0 0;
}
//表格
.list-table {
  margin: 25px auto 15px;
  width: 100%;
  border-collapse: collapse;
  th {
    background: #bbbec4;
    font-size: 16px;
    text-align: center;
    line-height: 30px;
    color: #fff;
    border: 1px solid #bbbec4;
  }
  td {
    border: 1px solid #bbbec4;
    padding: 5px;
    text-align: center;
    font-size: 16px;
    color: #282b32;
  }
  .no-data{
    color: #DDDEE1;
    padding: 10px 0;
  }
}
</style>



